<template>
  <div class="icon-title">
    <i :class="this.icon"></i>
    <h3>
      <slot></slot>
    </h3>
  </div>
</template>

<script>
export default {
  props: {
    icon: {
      required: false,
      default: 'fas fa-user',
      type: String
    }
  }
}
</script>

<style lang="scss" scoped>
.icon-title {
  display: flex;
  align-items: center;
  color: #666674;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 20px;

  i {
    margin-right: 20px;
    font-size: 25px;
  }

  h3 {
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 20px;
  }
}
</style>



