<template>
  <div class="list-config">
    <div class="header">
      <div class="title">
        <i class="fas fa-cog"></i>
        <span>Configuracao da Listagem</span>
      </div>
      <div class="close-button">
        <a href="#/">
          <i class="fas fa-times"></i>
        </a>
      </div>
    </div>
    <div class="body">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import SwitchFilter from '../switch/SwitchFilter'
export default {
  components: {
    'switch-filter': SwitchFilter
  }
}
</script>

<style lang="scss" scoped>
  .list-config {
    display: inline-block;
    webkit-box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    .header {
      display: flex;
      background-color: #0063b0;
      color: white;
      font-size: 14px;
      font-family: 'Roboto', sans-serif;
      font-weight: 600;
      width: 500px;
      height: 70px;
      justify-content: space-between;
      align-items: center;
      .title {
        margin-left: 30px;
        i {
          font-size: 20px;
          margin-right: 10px;
        }
      }
      .close-button {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
        background-color: rgba(0, 0, 0, .6);
        border-radius: 50%;
        padding: 8px;
        margin-right: 20px;
        a {
          color: white;
        }
      }
    }
    .body {
      background-color: white;
      width: 500px;
      padding-left: 25px;
      padding-right: 25px;
      padding-bottom: 20px;
      padding-top: 20px;
      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
          margin-bottom: 3px;
        }
      }
    }
  }
</style>



