<template>
  <tr class="table-row-mobi">
    <slot></slot>
  </tr>
</template>

<script>
import Checkout from '../checkout/Checkout'
import ActionButton from '../action-button/ActionButton.vue'
import StatusTag from '../status-tag/StatusTag.vue'
import TableText from './TableText.vue'
export default {
  components: {
    'checkout': Checkout,
    'action-button': ActionButton,
    'status-tag': StatusTag,
    'table-text': TableText

  }
}
</script>

<style lang="scss" scoped>
  .table-row-mobi {
    background-color: #f4f5f8;
    height: 56px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 30px;
    margin: 0;
    .check {
      margin-right: 28px;
    }
    td:nth-child(2) {
      width: 150px;
      text-align: left;
    }
    td:nth-child(3) {
      width: 165px;
      text-align: left;
    }
    td:nth-child(4) {
      width: 215px;
      text-align: left;
    }
    td:nth-child(5) {
      width: 150px;
      text-align: left;
    }
    td:nth-child(6) {
      width: 200px;
      text-align: center;
    }
    td:nth-child(7) {
      width: 100px;
      text-align: center;
      justify-content: center;
      display: flex;
    }
  }
</style>



