<template>
  <i class="icon" :class="this.icon"></i>
</template>

<script>
export default {
  props: {
    icon: {
      required: false,
      default: 'fas fa-question',
      type: String
    }
  }
}
</script>

<style lang="scss" scoped>
i {
  color: #666674;
  font-size: 20px;
}
</style>



