<template>
  <label>
    <input class='fa' type="radio" name="gender" value="female">
    <span class='interactive'>
    </span>
    <span class="text">
      <slot></slot>
    </span>
  </label>
</template>

<script>
export default {
  name: 'radiobutton'
}
</script>

<style scoped lang="scss">
// === Base
label {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  // text-align: center;
}

input {
  position: absolute;
  opacity: 0;
}

.text {
  font-family: Poppins;
  font-weight: 600;
  font-size: 14px;
  color: #646472;
}

.interactive {
  margin-top: 10px;
  margin-right: 25px;
}

label > input ~ .interactive:before {
  content: "";
  color: white;
  background: #20c99a;
  display: block;
  height: 22px;
  width: 22px;
  border-radius: 15px;
  border: 7px solid;
  border-color: #20c99a;
}

label > input:checked ~ .interactive:before {
  padding: 1px 2px;
  background: #fff;
}

// === Params
</style>



