<template>
  <h2 class="title-main" :class="titleStyle">
    <slot></slot>
  </h2>
</template>

<script>
export default {

  props: {
    estilo: {
      required: false,
      default: 'primary',
      type: String
    }
  },
  computed: {
    titleStyle () {
      if (this.estilo === 'primary') return 'title-main'
      if (this.estilo === 'secondary') return 'title-second'
      if (this.estilo === 'third') return 'title-third'
    }
  }

}
</script>

<style lang="scss" scoped>
.title-main {
  font-family: "Roboto", sans-serif;
  font-size: 26px;
  color: #434349;
  font-weight: 500;
}

.title-second {
  font-family: "Roboto", sans-serif;
  font-size: 20px;
  color: #666674;
  font-weight: 400;
}

.title-third {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: #a0a1ae;
  font-weight: 400;
}

h2 {
  margin-bottom: 0;
}
</style>



