<template>
<div class="text">
  <label :for="this.name">
    <slot></slot>
  </label>
  <input type="text" :placeholder="this.placeholder" :name="this.name">
  <small v-if="this.instruction">{{ instruction }}</small>
</div>
</template>

<script>
export default {

  props: {
    name: {
      required: true,
      type: String
    },
    placeholder: {
      required: false,
      type: String,
      default: 'Insert a value in the component'
    },
    instruction: {
      required: false,
      type: String,
      default: ''
    }
  }

}
</script>

<style lang="scss" scoped>

.text {
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  label {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #666674;
  }
  input {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: #666674;
    padding: .4em;
    width: 100%;
    border: 1px solid #ebedf2;
    margin-bottom: .3em;

  }
  small {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-size: 12px;
    color: #666674;
  }
}

</style>



