<template>
  <a href="#/" :class="bgColor" class='btn custom'>
    <i :class="this.icon" class="texto-branco"></i>
    <span class="tooltiptext">
      <slot></slot>
    </span>
  </a>
</template>

<script>
export default {
  name: 'action-button',
  props: {
    action: null,
    tooltip: null,
    color: {
      required: true,
      type: String,
      default: 'azul'
    },
    icon: {
      type: String,
      default: 'none'
    }
  },

  computed: {
    bgColor () {
      if (this.color != null) {
        return 'bg-' + this.color
      } else {
        return ''
      }
    },

    myTitle () {
      if (this.tooltip != null) {
        return this.tooltip
      } else {
        return ''
      }
    },

    faIcon () {
      var textColor = ''
      if (this.color === 'amarelo') {
        textColor = 'texto-preto'
      } else {
        textColor = 'texto-branco'
      }

      var myIcon = this.icon
      if (this.action != null) {
        switch (this.action) {
          case 'statistic':
            myIcon = 'far fa-chart-bar'
            break
          case 'print':
            myIcon = 'fas fa-print'
            break
          case 'new-segmentation':
            myIcon = 'fas fa-code-branch'
            break
          case 'details':
            myIcon = 'fa fa-list-alt'
            break
          case 'duplicate':
            myIcon = 'fas fa-reply-all'
            break
          case 'generate-report':
            myIcon = 'fas fa-file-alt'
            break
          case 'archive':
            myIcon = 'fas fa-archive'
            break
          case 'visualize':
            myIcon = 'far fa-eye'
            break
          case 'history':
            myIcon = 'fas fa-history'
            break
          case 'edit':
            myIcon = 'fa fa-list-alt'
            break
          case 'exclude':
            myIcon = 'fa fa-list-alt'
            break
          case 'word':
            myIcon = 'fas fa-file-word'
            break
          case 'excel':
            myIcon = 'fas fa-file-excel'
            break
          case 'pdf':
            myIcon = 'fas fa-file-pdf'
            break
        }
      }

      return myIcon + ' ' + textColor
    }
  }
}
</script>

<style scoped lang="scss">
.custom {
  position: relative;
  margin: 1px 2px;
  width: 33px;
  height: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Poppins;
  font-weight: 400;
  border: 1px solid transparent;
  z-index: 1;

  &:hover {
    .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
}

.tooltiptext {
  background-color: white;
  color: #575962;
  font-weight: 400;
  padding: 0;
  padding-left: 3px;
  padding-right: 3px;
  height: 15px;
  font-size: 10px;
  text-align: center;
  position: absolute;
  top:38px;
  z-index: 2;
  transition: opacity ease-in 1.2s;
  transition: opacity ease-out 1.2s;
  opacity: 0;
  visibility: hidden;
  -webkit-box-shadow: 0px 9px 32px 2px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 9px 32px 2px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 9px 32px 2px rgba(0, 0, 0, 0.1);
}

.bg-verde {
  background-color: #20c99a;
}

.bg-vermelho {
  background-color: #f4516c;
}

.bg-amarelo {
  background-color: #ffb822;
}

.bg-roxo {
  background: #7b20c9;
}

.bg-azul {
  background-color: #0081ff;
}

.bg-cinza {
  background-color: #666674;
}

.bg-word {
  background-color: #184e84;
}

.texto-branco {
  color: white;
}

.texto-preto {
  color: black;
}
</style>



