<template>
  <div class="option-config">
    <div class="title" :class="bgImageFun">
      <span><slot></slot></span>
    </div>
    <ul class="filter">
      <li>
        <a href="#/">
          <span>Hoje</span>
        </a>
      </li>
      <li>
        <a href="#/">
          <span>Semana</span>
        </a>
      </li>
      <li>
        <a href="#/">
          <span>Mes</span>
        </a>
      </li>
    </ul>
    <ul class="options">
      <li>
        <a href="#/">
          <span>Fulano</span>
        </a>
      </li>
      <li>
        <a href="#/">
          <span>Ciclano</span>
        </a>
      </li>
      <li>
        <a href="#/">
          <span>Beltrano</span>
        </a>
      </li>
      <li>
        <a href="#/">
          <span>De tal</span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    bgImage: {
      type: String,
      default: '',
      required: false
    }
  },
  computed: {
    bgImageFun () {
      if (this.bgImage) {
        return 'image'
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .option-config {
    font-family: "Poppins", sans-serif;
    color: #666674;
    font-weight: 400;
    font-size: 14px;
    height: 380px;
    width: 330px;
    border-radius: 5px;
    background-color: white;
    margin: auto auto;
    webkit-box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    // background-color: greenyellow;
    .title {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 330px;
      height: 120px;
      color: white;
      background-color: #003158;
    }
    .image {
      background-image: url('../../../assets/birthdaybg.png');
    }
    .filter {
      list-style: none;
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: center;
      padding: 20px 0;
      border-bottom: 1px solid #ebebeb;
      li {
        a {
          color: #575962;
          text-decoration: none;
          background-color: transparent;
          border-top-left-radius: 20px;
          border-bottom-left-radius: 20px;
          border-top-right-radius: 20px;
          border-bottom-right-radius: 20px;
          display: inline-block;
          min-width: 75px;
          &:focus {
            background-color: #0081ff;
            color: white;
          }
        }
      }
    }
    .options {
      list-style: none;
      margin: 0;
      padding: 0;
      text-align: left;
      padding: 0 20px;
      height: 143px;
      overflow-y: auto;
      li {
        border-bottom: 1px dashed #ebebeb;
        &:last-child {
          border-bottom: none;
        }
        a {
          display: inline-block;
          margin: 15px;
          font-size: 12px;
          color: #666674;
          text-decoration: none;
        }
      }
    }
  }
</style>



