import Vue from 'vue'
import Router from 'vue-router'
import Componentes from '@/components/shared/Componentes'
import Cadastros from '@/components/pages/Cadastros'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Componentes',
      component: Componentes
    },
    {
      path: '/cadastros',
      name: 'Cadastros',
      component: Cadastros
    }
  ],
  mode: 'history'
})



