<template>
  <li class="item">
    <a href="#">
      <i v-if="this.icon" :class="this.icon"></i>
      <slot></slot>
    </a>
    <div v-if="this.alerts" class="notify">
      <span>{{ alerts }}</span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    icon: {
      required: false,
      default: '',
      type: String
    },
    alerts: {
      required: false,
      default: '',
      type: String
    }
  }
}
</script>

<style lang="scss" scoped>
.item {
  margin: 0;
  margin-bottom: 1em;
  width: 100%;
  a {
    margin: 0;
    width: 100%;
    text-decoration: none;
    color: #676c7b;
    font-weight: 400;
    font-size: 13px;
    font-family: "Poppins", sans-serif;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    i {
      margin-right: 0.5em;
      font-size: 18px;
    }

    &:hover {
      color: #0081ff;
    }
  }

  .notify {
    background-color: #20c99a;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    span {
      margin: 0 auto;
      color: white;
      font-size: 10px;
      display: block;
    }
  }
}
</style>



