<template>
  <label>
    <input :class="bgColor" class='fa' type="checkbox" name="">
    <span>
      <slot></slot>
    </span>
  </label>
</template>

<script>
export default {
  name: 'checkout',
  props: {
    color: {
      required: true,
      type: String,
      default: 'azul'
    }
  },

  computed: {
    bgColor () {
      if (this.color != null) {
        return 'bg-' + this.color
      } else {
        return ''
      }
    }
  }
}
</script>

<style scoped lang="scss">
label {
  display: flex;
  justify-content: center;
  align-items: center;
}

span {
  font-family: Poppins;
  font-weight: 600;
  font-size: 14px;
  color: #646472;
}

input {
  margin-right: 25px;
}

span {
  margin-top: 10px;
}

input:before {
  content: "";
  color: white;
  background: #e5e3ef;
  display: block;
  height: 20px;
  width: 20px;
  border-radius: 3px;
  border: 1px solid;
}

input:checked:before {
  content: "\f00c";
  padding: 1px 2px;
}

.bg-verde:before {
  border: 1.2px solid #ebedf2;
  border-radius: 3px;
  // border-color: #ebedf2;
}

.bg-vermelho:before {
  border-color: #f4516c;
}

.bg-amarelo:before {
  border-color: #ffb822;
}

.bg-roxo:before {
  border-color: #7b20c9;
}

.bg-azul:before {
  border-color: #0081ff;
}

.bg-cinza:before {
  border-color: #666674;
}

.bg-verde:checked:before {
  background: #20c99a;
}

.bg-vermelho:checked:before {
  background: #f4516c;
}

.bg-amarelo:checked:before {
  background: #ffb822;
}

.bg-roxo:checked:before {
  background: #7b20c9;
}

.bg-azul:checked:before {
  background: #0081ff;
}

.bg-cinza:checked:before {
  background: #666674;
}
</style>



