<template>

  <div class="switch-filter">
    <span><slot></slot></span>
    <switch-toggle/>
  </div>

</template>

<script>
import Switch from './Switch'
export default {
  components: {
    'switch-toggle': Switch
  }
}
</script>

<style lang="scss" scoped>
.switch-filter {
  display: flex;
  justify-content: space-between;
  padding: 1em;
  background-color: #f4f5f8;
  align-items: center;
  height: 46px;
  width: 100%;
  border-radius: 10px;

  span {
    font-family: "Poppins", sans-serif;
    font-size: 13px;
    font-weight: 500;
    color: #575962;
  }
}
</style>



