<template>
  <div class="button-filter">
    <button class="filter">
      <i class="icon" :class="this.icon"></i>
      <span>
        <slot></slot>
      </span>
      <div class="down-arrow">
        <i class="fas fa-chevron-down"></i>
      </div>
    </button>
  </div>
</template>

<script>
export default {

  props: {
    icon: {
      default: 'fas fa-filter',
      required: 'true',
      type: String
    }
  }

}
</script>

<style lang="scss" scoped>
.button-filter {
  display: inline-block;

}
.filter {
  display: flex;
  margin: auto auto;
  align-items: center;
  font-family: "Poppins", sans-serif;
  font-size: 13px;
  font-weight: 500;
  color: white;
  background-color: #666674;
  border: none;
  height: 42px;
  padding-left: 1.5em;
  border-radius: 60px;
  cursor: pointer;
  box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.19) !important;
  transition: box-shadow 0.25s ease-in-out;

  &:hover {
    background-color: #5f5f6b;
    -webkit-box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.25) !important;
    -moz-box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.25) !important;
    box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.25) !important;
  }

  &:focus {
      outline: none;
  }

  span {
    margin-right: 0.9em;
    margin-left: 0.7em;
  }

  .down-arrow {
    display: flex;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: #858590;
    border-radius: 50%;

    i {
      margin: auto auto;
    }
  }

  .icon {
    font-size: 20px;
  }
}
</style>



