<template>

<span :class="estiloDoStatus" class="m-badge m-badge--wide">
    <slot></slot>
</span>

</template>

<script>
export default {

  props: {
    estilo: {
      required: false,
      default: 'atualizado',
      type: String
    }
  },

  computed: {
    estiloDoStatus () {
      if (this.estilo === 'atualizado') return 'status-atualizado'
      if (this.estilo === 'desatualizado') return 'status-desatualizado'
      if (this.estilo === 'pendente') return 'status-pendente'
      if (this.estilo === 'abriu') return 'status-abriu'
      if (this.estilo === 'atendimentos') return 'status-atendimentos'
    }

  }
}
</script>

<style scoped lang="scss">

.m-badge {
  border-radius: (20px / 2);
  background: white;
  font-family: "Poppins", sans-serif;
  color: white;
  font-weight: 400;
  font-size: 12px;
  line-height: 22px;
  min-height: 20px;
  min-width: 135px;
  vertical-align: middle;
  text-align: center;
  display: inline-block;
  padding: 0px 2px;

  &.m-badge--wide {
    letter-spacing: 0.6px;
    padding: 1px 20px;
  }

  &.m-badge--rounded {
    border-radius: (3px);
  }

  &.m-badge--dot {
    padding: 0;
    line-height: 6px;
    min-height: 6px;
    min-width: 6px;
    height: 6px;
    width: 6px;
    border-radius: (100%);
  }

  &.m-badge--dot-small {
    padding: 0;
    line-height: 4px;
    min-height: 4px;
    min-width: 4px;
    height: 4px;
    width: 4px;
    border-radius: (100%);
  }
}

.status-atualizado {
  background-color: #20c99a;
  color: white;
}

.status-desatualizado {
  background-color: #f4516c;
  color: white;
}

.status-pendente {
  background-color: #ffb822;
  color: #222222;
}

.status-abriu {
  background-color: #7b20c9;
  color: white;
}

.status-atendimentos {
  background-color: #0081ff;
  color: white;
}

</style>



