<template>
  <tr class="head">
    <slot></slot>
  </tr>
</template>

<script>
import Checkout from '../checkout/Checkout'
import LabelFilter from './LabelFilter'
import LabelText from './LabelText'
export default {
  components: {
    'checkout': Checkout,
    'label-filter': LabelFilter,
    'label-text': LabelText
  }
}
</script>

<style lang="scss" scoped>
  .head {
    background-color: #f4f5f8;
    height: 56px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 30px;
    .check {
      margin-right: 28px;
    }
    th:nth-child(2) {
      width: 150px;
      text-align: left;
    }
    th:nth-child(3) {
      width: 165px;
      text-align: left;
    }
    th:nth-child(4) {
      width: 215px;
      text-align: left;
    }
    th:nth-child(5) {
      width: 150px;
      text-align: left;
    }
    th:nth-child(6) {
      width: 200px;
      text-align: center;
    }

    th:nth-child(7) {
      width: 100px;
      text-align: center;
    }
  }
</style>



