<template>
  <div class="option-config">
    <div class="title">
      <img src="https://images.pexels.com/photos/364100/pexels-photo-364100.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940" alt="">
      <div class="name">
        <span>Lucas Augusto</span>
        <small>lucasaugusto@criasol.com.br</small>
      </div>
    </div>
    <ul class="options">
      <li>
        <a href="#/">
          <span>Minha conta</span>
        </a>
      </li>
    </ul>
    <div class="logout">
      <button-air estilo="secundario">Logout</button-air>
    </div>
  </div>
</template>

<script>
import ButtonAir from '../botoes/ButtonAir'
export default {
  components: {
    'button-air': ButtonAir
  }
}
</script>

<style lang="scss" scoped>
  .option-config {
    font-family: "Poppins", sans-serif;
    color: #666674;
    font-weight: 400;
    font-size: 14px;
    background-color: white;
    height: 270px;
    border-radius: 5px;
    width: 330px;
    margin: auto auto;
    webkit-box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    box-shadow: -6px 5px 44px -11px rgba(0, 0, 0, 0.75);
    .title {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 330px;
      height: 120px;
      color: white;
      background-image: url('../../../assets/loginbg.png');
      img {
        height: 70px;
        width: 70px;
        border-radius: 50%;
        margin-right: 20px;
      }
      .name {
        text-align: left;
        width: 60%;
      }
    }
    .options {
      list-style: none;
      margin: 0;
      padding: 0;
      text-align: left;
      li {
        border-bottom: 1px solid #ebebeb;
        a {
          display: inline-block;
          margin: 22px;
          color: #666674;
          text-decoration: none;
        }
      }
    }
    .logout {
      display: flex;
      justify-content: flex-start;
      margin-left: 10px;
      height: 85px;
      align-items: center;
    }
  }
</style>



