<template>
  <div class="top-menu">
    <div class="logo">
      <img src="../../../assets/logo.png" alt="">
    </div>
    <ul class="infos">
      <li class="account">
        <a class="saudacao" href="#/">
          <span>Ola,</span> Lucas</a>
        <div class="account-window">
          <my-account></my-account>
        </div>
      </li>
      <li class="birthday">
        <a href="#/" class="top-item">
          <i class="fas fa-birthday-cake"></i>
          <div class="badge">
            <span>23</span>
          </div>
        </a>
        <div class="birth-window">
          <filter-window bgImage=true>Aniversariantes</filter-window>
        </div>
      </li>
      <li class="alerts">
        <a href="#/" class="top-item">
          <i class="fas fa-bell"></i>
          <div class="badge">
            <span>23</span>
          </div>
        </a>
        <div class="notify-window">
          <filter-window>Notificacoes</filter-window>
        </div>
      </li>
      <li class="config">
        <a href="#/" class="top-item">
          <i class="fas fa-cog"></i>
        </a>
        <div class="config-window">
          <config-window></config-window>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import MyAccount from '../option-window/MyAccount'
import FilterWindow from '../option-window/FilterWindow'
import ConfigWindow from '../option-window/ConfigWindow'
export default {
  components: {
    'my-account': MyAccount,
    'filter-window': FilterWindow,
    'config-window': ConfigWindow
  }
}
</script>

<style lang="scss" scoped>
.top-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #0081ff;
  height: 120px;
  padding-left: 5em;
  padding-right: 5em;
  .infos {
    display: flex;
    font-family: "Roboto", sans-serif;
    align-items: center;
    justify-content: flex-end;
    font-weight: 600;
    width: 500px;
    color: white;
    font-size: 14px;
    list-style-type: none;
    margin: 0;
    padding: 0;
    .account {
      position: relative;
      .saudacao {
        span {
          font-weight: 500;
        }
        font-size: 14px;
        text-decoration: none;
        font-weight: 700;
        &:focus {
          + .account-window {
            opacity: 1;
            top: 180%;
            display: block;
            visibility: visible;
            transition: opacity 0.65s ease-in-out;
            transition: top 0.45s ease-in-out;
          }
        }
      }
      .account-window {
        position: absolute;
        opacity: 0;
        visibility: hidden;
        z-index: 10;
        right: -40%;
        top: 200%;
        transition: opacity 0.65s ease-in-out;
        transition: top 0.45s ease-in-out;
      }
    }
    .birthday {
      position: relative;
      a {
        &:focus {
          + .birth-window {
            opacity: 1;
            top: 150%;
            visibility: visible;
            transition: opacity 0.65s ease-in-out;
            transition: top 0.45s ease-in-out;
          }
        }
      }
      .birth-window {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        z-index: 10;
        right: -330%;
        top: 170%;
      }
    }
    .alerts {
      position: relative;
      a {
        &:focus {
          + .notify-window {
            opacity: 1;
            top: 150%;
            visibility: visible;
            transition: opacity 0.65s ease-in-out;
            transition: top 0.45s ease-in-out;
          }
        }
      }
      .notify-window {
        opacity: 0;
        position: absolute;
        visibility: hidden;
        z-index: 10;
        right: -250%;
        top: 170%;
      }
    }
    .config {
      position: relative;
      width: 10px;
      a {
        &:focus {
          + .config-window {
            opacity: 1;
            top: 150%;
            visibility: visible;
            transition: opacity 0.65s ease-in-out;
            transition: top 0.45s ease-in-out;
          }
        }
      }
      .config-window {
        opacity: 0;
        position: absolute;
        visibility: hidden;
        z-index: 10;
        right: -590%;
        top: 170%;
      }
    }
    li {
      margin-right: 15px;
      .top-item {
        background-color: #0079ef;
        border-radius: 50%;
        display: flex;
        position: relative;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        height: 40px;
        width: 40px;
        .badge {
          position: absolute;
          background-color: red;
          width: 25px;
          height: 25px;
          top: -20%;
          right: -20%;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
          span {
            font-size: 11px;
          }
        }
        i {
          font-size: 19px;
        }
      }
      &:last-child {
        margin-left: 10px;
        a {
          background-color: transparent;
          width: 0;
        }
      }
    }
    a {
      color: white;
    }
  }
}
</style>



