<template>
  <div class="toast" :class="this.class">
    <div class="message">
      <i :class="this.icon"></i>
      <span><slot></slot></span>
    </div>
    <div class="close">
      <a href="">
        <i class="fas fa-times"></i>
      </a>
    </div>
  </div>
</template>

<script>
export default {

  data () {
    return {
      icon: '',
      class: ''
    }
  },

  props: {
    type: {
      required: false,
      default: 'success',
      type: String
    }
  },
  created () {
    switch (this.type.toLowerCase()) {
      case 'success':
        this.icon = 'fas fa-check'
        this.class = 'toast-success'
        return
      case 'warning':
        this.icon = 'fas fa-exclamation-triangle'
        this.class = 'toast-warning'
        return
      case 'danger':
        this.icon = 'fas fa-exclamation-triangle'
        this.class = 'toast-danger'
        return
      default:
        this.icon = 'fas fa-check'
        this.class = 'toast-success'
    }
  }

}
</script>

<style lang="scss" scoped>
.toast {
  background-color: #34bfa3;
  font-family: "Poppins", sans-serif;
  width: 390px;
  height: 40px;
  border-radius: 5px;
  box-shadow: 0 0 15px 1px rgba(69, 65, 78, 0.2);
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  padding-right: 0.9em;
  padding-left: 0.9em;
  display: flex;
  align-items: center;
  .message {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    color: white;
    span {
      font-size: 13px;
    }
    i {
      margin-right: 1em;
      font-size: 20px;
    }
  }
  .close {
    a {
      display: flex;
      align-items: center;
      text-decoration: none;
    }
    i {
      color: white;
      opacity: 0.9;
      font-size: 12px;
    }
  }
}

.toast-success {
  background-color: #34bfa3;
}

.toast-warning {
  background-color: #ffb822;
}

.toast-danger {
  background-color: #f4516c;
}
</style>



