<template>
  <nav class="menu-bottom">
    <div class="container">
      <div class="menu-inner">
        <div class="menu-items">
          <ul class="list-items">
            <li>
              <a href="#/">
                <span>Dashboard</span>
              </a>
            </li>
            <drop-menu>
              <span slot="item-name">Cadastros</span>
              <template slot="item-list">
                <drop-item alerts=1 icon="far fa-chart-bar">Generate Reports</drop-item>
                <drop-item alerts=2 icon="fab fa-android">Generate Reports</drop-item>
                <drop-item alerts=3 icon="fab fa-app-store-ios">Generate Reports</drop-item>
                <drop-item alerts=4 icon="fab fa-apple">Generate Reports</drop-item>
              </template>
            </drop-menu>
            <li>
              <a href="#/">
                <span>Mobilizao</span>
              </a>
            </li>
            <li>
              <a href="#/">
                <span>Atendimento</span>
              </a>
            </li>
            <drop-menu>
              <span slot="item-name">Automao</span>
              <template slot="item-list">
                <drop-item icon="far fa-chart-bar">Generate Reports</drop-item>
                <drop-item icon="fab fa-android">Generate Reports</drop-item>
                <drop-item icon="fab fa-app-store-ios">Generate Reports</drop-item>
                <drop-item icon="fab fa-apple">Generate Reports</drop-item>
              </template>
            </drop-menu>
          </ul>
        </div>
        <search-bar/>
      </div>
    </div>
  </nav>

</template>

<script>
import SearchBar from '../searchbar/SearchBar'
import DropDownMenu from './DropDownMenu'
import DropDownItem from './DropDownItem'

export default {

  components: {
    'search-bar': SearchBar,
    'drop-menu': DropDownMenu,
    'drop-item': DropDownItem
  }
}
</script>

<style lang="scss" scoped>
.menu-bottom {
  width: 100%;
  background-color: #0079ef;
  height: 70px;

  .container {
    height: 100%;
  }

  .menu-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }

  .menu-items {
    .list-items {
      display: flex;
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        margin-right: 2em;
        display: flex;
        align-items: center;
        a {
          &:hover {
            text-decoration: none;
          }
        }

      }

      span {
        color: white;
      }
    }
  }
}
</style>



